* {
  font-family: 'Noto Sans Thai', sans-serif;
}

body {
  background: #F5F5F5
}

.card {
  border: none;
}

section {
  padding-top: 1%;
  padding-bottom: 1%;
}

.pointer {
  cursor: pointer;
}

/* Page Home */
.bold-pur {
  font-weight: 600;
  color: #764C5E;
}

.subtitle {
  font-weight: 600;
  color: rgba(118, 76, 94, 0.80);
}

.descript {
  color: rgba(0, 0, 0, 0.55);
  text-indent: 60px;
}

.behaviour-descript {
  color: rgba(0, 0, 0, 0.85);
  text-indent: 60px;
}

.truncate-text {
  display: -webkit-box;
  -webkit-line-clamp: 7;
  /* Number of lines to display */
  -webkit-box-orient: vertical;
  overflow: hidden;
}


.img-backg {
  background: url('../public/Image/background.png');
  background-blend-mode: multiply;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.t-studentnum {
  font-size: 5rem;
  font-weight: 800;
  color: white;
}

.card-teacher {
  max-width: 18rem;
  margin-top: 1.5%;
  border-radius: 5px;
  background: #FFF;
  box-shadow: 0px 4px 15px 0px rgba(0, 0, 0, 0.10);
}

.color {
  &.theme {
    color: #764C5E;
  }

  &.bgegg {
    background: #F0F0F0;
  }

  &.themeblue {
    color: #8CB5E4;
  }

  &.themepink {
    color: #E69BBC;
  }
}

.text-classroom {
  color: rgba(0, 0, 0, 0.55);
}


/* Login Page */
.circleLog {
  width: 35vw;
  height: 35vw;
  box-shadow: -8px 6px 25px 5px rgba(0, 0, 0, 0.20);
  border-radius: 50%;

  &.parent-b {
    background: #8CB5E4 !important;
  }

  &.teacher-p {
    background: #E69BBC !important;
  }
}


.circle {
  position: relative;
  display: block;
  margin: 2em 0;
  background-color: transparent;
  color: #222;
  text-align: center;
  margin: 0;
  /* left: 25%; */
}

.circle:after {
  display: block;
  padding-bottom: 100%;
  width: 100%;
  height: 0;
  border-radius: 50%;
  background-color: white;
  content: "";
}

.circle__inner {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 50%;
  height: 100%;
}

.circle__wrapper {
  display: table;
  width: 200%;
  height: 100%;
}

.circle__content {
  display: table-cell;
  /* padding: 1rem; */
  font-size: 3em;
  vertical-align: middle;
  color: #FFF;
}

.icon-log {
  font-size: 60px;
  color: white;
}

