/* Navbar for Home Page  */
.grads {
    background: rgb(166, 168, 168);
    background: linear-gradient(0deg, rgba(166, 168, 168, 0) 0%, rgba(51, 51, 51, 1) 100%);
}

.nav-color {
    background-color: #764C5E;
}

.grads-teacher {
    background: rgb(211, 211, 211);
    background: linear-gradient(0deg, rgba(166, 168, 168, 0) 0%, rgb(181, 181, 181) 100%);
}

.dropdown-menu[data-bs-popper] {
    left: -230% !important;
}

.dropdown-toggle::after {
    color: #764C5E;
}

.color-theme {
    color: #764C5E !important;
}

.nav-icon {
    font-size: 250%;
    color: #764C5E;
}

/* navbar */
.sidebar {
    position: fixed;
    top: 0;
    right: -250px;
    /* Sidebar initially hidden */
    width: 250px;
    height: 100%;
    background-color: #EFEEEE;
    backdrop-filter: blur(10px);
    color: #764C5E !important;
    transition: right 0.3s ease-in-out;
    padding: 20px;
    z-index: 1;

}

.sidebar.show {
    right: 0;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    /* 70% opacity black */
    z-index: 1;
    /* Ensure the overlay is on top of the sidebar */
    display: none;
    /* Initially hidden */
}

.overlay.show {
    display: block;
}

.navbar {
    background-color: rgba(241, 35, 35, 0);
}

.lower-menu-item {
    margin-top: 420px;
    /* Adjust the margin-top value as needed */
}

.active-n {
    font-weight: 600;
    border-bottom: solid;
    width: 130px;
}

.active-slide {
    color: #E69BBC;
    font-weight: 600;
}

.navbar-nav {
    --bs-nav-link-color: none;
    --bs-nav-link-hover-color: none;
    --bs-nav-link-disabled-color: none;
}

@media(max-width: 991px) {}