.banner {
    background: url('../../public/Image/teacherbackground.png');
    background-blend-mode: multiply;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.color {
    &.theme {
        color: #764C5E;
    }

    &.themeblue {
        color: #8CB5E4;
    }

    &.themepink {
        color: #E69BBC;
    }

    &.themegrey {
        color: rgba(0, 0, 0, 0.55);
    }
}

