.circle-teacher {
    position: relative;
    display: block;
    margin: 2em 0;
    background-color: transparent;
    color: #222;
    text-align: center;
    margin: 0;
    /* left: 25%; */
}

.circle-teacher:after {
    display: block;
    padding-bottom: 50%;
    width: 50%;
    height: 0;
    border-radius: 50%;
    background-color: white;
    content: "";
}

.circle-teacher__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 50%;
    height: 100%;
}

.circle-teacher__wrapper {
    display: table;
    width: 200%;
    height: 100%;
}

.circle-teacher__content {
    display: table-cell;
    /* padding: 1rem; */
    font-size: 4em;
    vertical-align: middle;
    color: #FFF;
}

.circle-homework {
    position: relative;
    display: block;
    margin: 2em 0;
    background-color: transparent;
    text-align: center;
    margin: 0;
    /* left: 25%; */
}

.circle-homework:after {
    display: block;
    padding-bottom: 50%;
    width: 50%;
    height: 0;
    border-radius: 50%;
    background-color: #E69BBC;
    content: "";
}

.circle-homework__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 50%;
    height: 100%;
}

.circle-homework__wrapper {
    display: table;
    width: 200%;
