}


.circle-student {
    position: relative;
    display: block;
    margin: 2em 0;
    background-color: transparent;
    margin: 0;
    /* left: 25%; */
}

.circle-student:after {
    display: block;
    padding-bottom: 50%;
    width: 100%;
    height: 0;

    border-radius: 50%;
    content: "";
}

.circle-student__inner {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 25%;
    width: 50%;
    height: 100%;
}

.circle-student__wrapper {
    display: table;
    width: 100%;
    height: 100%;
}




