.banner {
    background: url('../../public/Image/teacherbackground.png');
    background-blend-mode: multiply;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

a {
    text-decoration: none;
}

.textcut {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.truncate-indent {
    text-indent: 50px;
    display: -webkit-box;
    -webkit-line-clamp: 7;
    /* Number of lines to display */
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.shadowcard {
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.15);
}

.color {
    &.bgtheme {
        background: #764C5E;
    }

    &.bgpink {
        background: #E69BBC;
    }

    &.bgstate {
        background: #8CB5E4;
    }

    &.bgbeforebackhome {
        background: #F5B75B;
    }

    &.bgafterbackhome {
        background: #00C820B2;
    }

    &.bginout {
        background: rgba(0, 161, 26, 0.30);
    }

    &.theme {
        color: #764C5E;
    }

    &.themeblue {
        color: #8CB5E4;
    }

    &.themepink {
        color: #E69BBC;
    }
}

.in-shadow {
    border-radius: 5px;
    background: #FFF;
    box-shadow: 0px 4px 20px 3px rgba(0, 0, 0, 0.15);
}

/* ปุ่มธรรมดา */
.btn-theme {
    --bs-btn-color: #fff;
    --bs-btn-bg: #764C5E;
    --bs-btn-border-color: #764C5E;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #702d49;
    --bs-btn-hover-border-color: #702d49;
    --bs-btn-focus-shadow-rgb: 130, 138, 145;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #764b5d;
    --bs-btn-active-border-color: #764b5d;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #7b495f;
    --bs-btn-disabled-border-color: #7b495f;
}

.btn-themepink {
    --bs-btn-color: #fff;
    --bs-btn-bg: #E69BBC;
    --bs-btn-border-color: #E69BBC;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #e37aa7;
    --bs-btn-hover-border-color: #e37aa7;
    --bs-btn-focus-shadow-rgb: 130, 138, 145;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #eb91b8;
    --bs-btn-active-border-color: #eb91b8;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #d57da3;
    --bs-btn-disabled-border-color: #d57da3;
}

/* ปุ่มโปร่ง */
.btn-outline-theme {
    --bs-btn-color: #764C5E;
    --bs-btn-border-color: #764C5E;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #764C5E;
    --bs-btn-hover-border-color: #764C5E;
    --bs-btn-focus-shadow-rgb: 108, 117, 125;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #764C5E;
    --bs-btn-active-border-color: #764C5E;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #764C5E;
    --bs-btn-disabled-bg: transparent;
    --bs-btn-disabled-border-color: #764C5E;
    --bs-gradient: none;
}

.btn-outline-themepink {
    --bs-btn-color: #E69BBC;
    --bs-btn-border-color: #E69BBC;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #E69BBC;
    --bs-btn-hover-border-color: #E69BBC;
    --bs-btn-focus-shadow-rgb: 108, 117, 125;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #E69BBC;
    --bs-btn-active-border-color: #E69BBC;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #E69BBC;
    --bs-btn-disabled-bg: transparent;
    --bs-btn-disabled-border-color: #E69BBC;
    --bs-gradient: none;
}


.valid-feedback.feedback-icon,
.invalid-feedback.feedback-icon {
    position: absolute;
    width: auto;
    bottom: 10px;
    right: 10px;
    margin-top: 0;
}

.form-control.is-valid {
    background-image: none;
    border-color: rgba(0, 0, 0, 0) !important;
}


.pink-underline {
    width: 10%;
    height: 2.5px;
    background-color: #E69BBC;
    margin-top: -1%;
    margin-left: 1%;
}

.swiper {
    width: 100%;
    height: 100%;
}

:root {
    --swiper-theme-color: #E69BBC !important;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

/* .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
} */

@media (max-width: 767px) {
    .pink-underline {
        width: 15%;
        height: 2.5px;
        margin-left: 2%;
    }
}